import { useEffect, useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { Navbar as BSNavbar, Nav, Container, NavDropdown } from 'react-bootstrap';

export default function Navbar() {
  const [transparent, setTransparent] = useState(false);

  useEffect(() => {
    const onScroll = () => setTransparent(window.scrollY > 50);
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  return (
    <BSNavbar
      sticky="top"
      expand="lg"
      className={`py-3 ${transparent ? 'navbar-transparent' : 'navbar-colored'}`}
    >
      <Container>
        <Link href="/" className="d-flex align-items-center navbar-brand gap-2">
          <Image
            src="/images/logo_bloom_colores.png"
            alt="Bloom"
            width={35}
            height={35}
            priority
          />
          Bloom Fisio
        </Link>

        <BSNavbar.Toggle aria-controls="bloom-nav" />
        <BSNavbar.Collapse id="bloom-nav">
          <Nav className="ms-auto gap-2">
            <Link href="/nosotros" className="nav-link">
              Nosotros
            </Link>

            <Link href="/citas" className="nav-link">
              Citas
            </Link>

            <NavDropdown title="Servicios" id="servicios-dropdown">
              <Link href="/servicios/fisioterapia" className="dropdown-item">
                Fisioterapia
              </Link>
              <Link href="/servicios/estimulacion" className="dropdown-item">
                Estimulación
              </Link>
              <Link href="/servicios/preparacion-fisica" className="dropdown-item">
                Preparación física
              </Link>
            </NavDropdown>
          </Nav>
        </BSNavbar.Collapse>
      </Container>
    </BSNavbar>
  );
}